import { z } from 'zod';
import { FilterInterface, FilterSchema } from './filter.interface';

export const SegmentSchema = z.intersection(
  FilterSchema,
  z.object({
    name: z.string().min(1).optional(),
    description: z.string().min(1).optional(),
    public: z.boolean().optional(),
  }),
);

export interface SegmentInterface extends FilterInterface {
  name?: string;
  description?: string;
  public?: boolean;
}
